.StartButton button {
  font-size: 1.5em;
  color: white;
  background-color: rgba(255, 255, 255, 0.15); 
  border: none;               
  border-radius: 1em;       
  padding: 0.5em 1.5em;      
  cursor: pointer;           
  transition: all 0.2s ease;
}

.StartButton button:hover {
  color: #db6ca9;
  background-color: rgba(255, 255, 255, 0.4); 
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.2);
  transform: scale(1.02);
  
}


.glow-button {
  padding: 12px 24px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.15);
  color: #db6ca9;
  cursor: pointer;
  transition: all 0.3s ease;
}

.glow-button:hover {
  box-shadow: 0 0 10px #db6ca9, 0 0 20px #db6ca9;
}


.glow-button.loading {
  animation: glowPulse 1s infinite alternate;
  cursor: progress;
}

@keyframes glowPulse {
  0% {
    color: #ffeff8;
    background-color: rgba(255, 255, 255, 0.4);
    box-shadow: 0 0 15px #FFFFFFCC, 0 0 20px #FFFFFFCC;
  }
  100% {
    color: #0046F3FF;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 0 25px #f1d4ff, 0 0 30px #f1d4ff;
  }
}

@media (max-width: 600px) {
  .StartButton button {
    font-size: 0.8em;
    border-radius: 1em;
    padding: 0.5em 1em;
    cursor: pointer;
    transition: all 0.2s ease;
  }
}
